import { drawing } from "./utils";

export default class Ship {
  constructor(x, y, imgArray, width, height, context) {
    this.x = x;
    this.y = y;
    this.imgArray = imgArray;
    this.width = width;
    this.height = height;
    this.index = 0;
    this.context = context;

    this.animateShip();
  }

  update(x, y) {
    this.x = x;
    this.y = y;
  }

  draw() {
    drawing(
      this.context,
      this.imgArray[this.index],
      this.x,
      this.y,
      this.width,
      this.height
    );
  }

  animateShip() {
    setInterval(() => {
      let lastIndex = this.imgArray.length - 1;
      if (this.index === lastIndex) {
        this.index = 0;
      } else {
        this.index = this.index + 1;
      }
    }, 50);
  }
}
